  methods: {
    search: function(){
      for (var i = 0; i < this.products.length; i++) {
        if (this.products[i].title.toLowerCase() != this.inputTitle.toLowerCase()) {
          this.alert = true;          
        }
        else
        {
          this.infoProduct.urlImg = this.products[i].image.src;
          this.infoProduct.title = this.products[i].title;
          this.infoProduct.price = this.products[i].variants[0].price;
          this.infoProduct.pre_price = this.products[i].variants[0].compare_at_price;
          this.infoProduct.discount = Math.round(((1-(parseInt(this.products[i].variants[0].price)/parseInt(this.products[i].variants[0].compare_at_price)))*100)*100)/100;
          if(this.products[i].variants[0].inventory_management == null && 
             this.products[i].variants[0].inventory_policy == "deny" && 
             this.products[i].variants[0].inventory_quantity > 0)
          {
            this.infoProduct.status = this.products[i].variants[0].inventory_quantity + " left"
          }
          else {
            this.infoProduct.status = "Available"
          }          

          this.found = true;
          this.alert = false;
          console.log(this.alert)
          break;
        }
      }
    }
